*{
  padding:0;
  margin:0;
  box-sizing: border-box;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.whole-card{
  width:400px;
  height: 450px;
  overflow: hidden;
  background:linear-gradient(#e66465, #9198e5);
}
.det-card{
  padding-top:10px;
  width:400px;
  height: 80px;
  text-align: center;
  justify-content: center;
  font-size: 10px;

}
.det-card h3{
  color:maroon;
}
.image-card{
  width:500px;
  height: 300px;
  overflow: hidden;

  


}
.image-card img{
	width: 75%;
	height: 300px;
  padding-left:20px;
}


.button-card{
 width:400px;
 height: 50px;
 padding-left:150px;
 padding-top:5px;


}
.button-card button{
  border-radius:5px;
  padding:5px;
  background-color: rgb(241, 219, 94);
}
.grid-card{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap:20px;
  padding-left: 45px;
  padding-bottom: 45px;
}
.Cart-button{
  padding: 10px;
  background-color: rgb(235, 114, 98);
  cursor: pointer;
  border-color:  rgb(235, 114, 98);
  
 
}
header{
  height: 60px;
  background-color:chartreuse;
  padding-left: 43.3%;
  padding-top: 10px;
}
.product{
  text-align: center;
  color: aqua;
  text-shadow:2px 2px red;
  padding: 20px;
  font-size: 40px;
}
.cart{
  text-align: center;
  color: aqua;
  text-shadow:2px 2px red;
  padding: 20px;
  font-size: 40px;
}
.cost{
  font-family: 'Pacifico', cursive;
  text-align: center;
  color: rgb(74, 89, 218);
  text-shadow:2px 2px rgb(90, 35, 163);
  padding: 20px;
  font-size: 40px;
}

.cost span{

  color: rgb(255, 166, 0);

}
.clear-cart{
  height: 60px;

  padding-left: 46.3%;
  padding-bottom: 30px;


}
.clear-cart button{
  padding: 15px;
  background-color:rgb(255, 206, 206);
  color: rgb(240, 156, 0);
  text-shadow:2px 1px red;
  font-size: 20px;
  border-radius:50px;
  border-color: rgb(255, 206, 206);
  cursor: pointer;

  

}
.det-card span{
  color: rgb(255, 140, 0);
  text-shadow:2px 2px rgb(68, 10, 10);
  font-size: 30px;
}

.cart-App{
  background: rgb(235, 202, 225);
  min-height:100vh;
}

